import React, { useEffect, useState } from 'react';
import { CookiesProvider, useCookies } from "react-cookie";

const CookiesPrompt = () => {
    let [cookiesConfirmed, setCookiesConfirmed] = useState(false)
    const [confirmationCookie, setCookie] = useCookies(["CookieConsent"]);

    useEffect(() => {(confirmationCookie.CookieConsent !== null && confirmationCookie.CookieConsent === "true") && setCookiesConfirmed(true)}, [])

    let handleCookie = ()=>{
        setCookie("CookieConsent", "true", {
            path: "/"
          });
        setCookiesConfirmed(true)
    }

    return (
        <>
            {!cookiesConfirmed &&
            <div id="cookiesPromptWindow">
                <div style={{display:"flex", flexDirection:"row", width:"98%", justifyContent:"flex-end", padding:"0"}}><span className="material-symbols-outlined" style={{cursor:"pointer"}} onClick={()=>setCookiesConfirmed(true)}>close</span></div>
                <div id = "cookiesPromptElements" style = {{}}>
                    <div style={{margin:"5px"}}>Paspausdama(s) "Sutinku" arba toliau narydama(s) i svetain, neprietarauju kad mano renginyje dl narymo patirties pagerinimo bt isaugomi slapukai. </div>
                    <div style={{display:"flex", flexDirection:"row", alignItems:"center", justifyContent:"center"}}><input type="button" value="Sutinku" onClick={()=>{handleCookie()}} style={{backgroundColor:"#eb4034", border:"0px", padding:"10px", margin:"5px"}}></input></div>
                </div>
            </div>}
        </>
    );
};

export default CookiesPrompt;